import * as Yup from 'yup';
import PropTypes from 'prop-types';
import React, { useCallback } from 'react';
import { useSnackbar } from 'notistack';
import { useNavigate } from 'react-router-dom';
import { isValid as isValidDate } from 'date-fns';
import { Form, FormikProvider, useFormik } from 'formik';
// material
import { Save as SaveIcon } from '@mui/icons-material';
import { DateTimePicker, LoadingButton } from '@mui/lab';
import {
  Box,
  Card,
  Grid,
  Stack,
  Switch,
  TextField,
  Typography,
  FormHelperText,
  FormControlLabel,
  CardHeader,
  CardContent,
  InputAdornment,
  Link,
} from '@mui/material';
// utils
import { fData } from '../../utils/formatNumber';
// routes
import { PATH_DASHBOARD } from '../../routes/paths';
// utils
import { nameRegExp } from '../../utils/constants/regexs';
// components
import { UploadAvatar, UploadMultiFile } from '../upload';

// ----------------------------------------------------------------------

const NewBonusSchema = Yup.object().shape({
  images: Yup.array().min(1, 'Minimum 1 bonus image required.').max(3, 'Upload maximum 3 images.'),
  thumbnail: Yup.mixed().required('Thumbnail is required'),
  name: Yup.string().matches(nameRegExp, 'Cannot include special characters.').required('Name is required'),
  giveawayType: Yup.string(),
  status: Yup.boolean(),
  description: Yup.string(),
  startDate: Yup.date().nullable(true),
  value: Yup.number().required('This field is required.'),
  availableQuantity: Yup.number(),
  brand: Yup.string().required('Brand is required'),
});

BonusNewForm.propTypes = {
  isEdit: PropTypes.bool,
  currentBonus: PropTypes.object,
};

export default function BonusNewForm({ isEdit, currentBonus }) {
  const navigate = useNavigate();
  const { enqueueSnackbar } = useSnackbar();

  const formik = useFormik({
    enableReinitialize: true,
    initialValues: {
      images: currentBonus?.images || [],
      brand: currentBonus?.brand || 'Adidas',
      thumbnail: currentBonus?.thumbnail || null,
      name: currentBonus?.name || '',
      giveawayType: currentBonus?.giveawayType || 'Level2',
      status: currentBonus?.status || true,
      description: currentBonus?.description || '',
      startDate: currentBonus?.startDate ? new Date(currentBonus.startDate) : null,
      value: currentBonus?.value || null,
      availableQuantity: currentBonus?.availableQuantity || null,
    },
    validationSchema: NewBonusSchema,
    onSubmit: async (values, { setSubmitting, resetForm, setErrors }) => {
      alert(JSON.stringify(values));
      try {
        resetForm();
        setSubmitting(false);
        enqueueSnackbar(!isEdit ? 'Create success' : 'Update success', { variant: 'success' });
        navigate(PATH_DASHBOARD.bonus.list);
      } catch (error) {
        console.error(error);
        setSubmitting(false);
        setErrors(error);
      }
    },
  });

  const { errors, values, touched, handleSubmit, isSubmitting, setFieldValue, getFieldProps } = formik;

  console.log('*** ', values, errors);

  const handleImageDrop = useCallback(
    (acceptedFiles) => {
      setFieldValue(
        'images',
        acceptedFiles.map((file) =>
          Object.assign(file, {
            preview: URL.createObjectURL(file),
          })
        )
      );
    },
    [setFieldValue]
  );

  const handleRemoveAll = () => {
    setFieldValue('images', []);
  };

  const handleRemove = (file) => {
    const filteredItems = values.images.filter((_file) => _file !== file);
    setFieldValue('images', filteredItems);
  };

  const handleThumbnailDrop = useCallback(
    (acceptedFiles) => {
      const file = acceptedFiles[0];
      if (file) {
        setFieldValue('thumbnail', {
          ...file,
          preview: URL.createObjectURL(file),
        });
      }
    },
    [setFieldValue]
  );

  return (
    <FormikProvider value={formik}>
      <Form noValidate autoComplete="off" onSubmit={handleSubmit}>
        <Grid container spacing={3}>
          <Grid item xs={12} md={6}>
            <Stack direction={{ xs: 'column', sm: 'row' }} spacing={{ xs: 3, sm: 2 }}>
              <Card>
                <CardHeader title="Bonus Images" />
                <CardContent>
                  <Box sx={{ mb: 5 }}>
                    <UploadMultiFile
                      showPreview
                      maxSize={3145728}
                      accept="image/*"
                      files={values.images}
                      onDrop={handleImageDrop}
                      onRemove={handleRemove}
                      onRemoveAll={handleRemoveAll}
                      error={Boolean(touched.images && errors.images)}
                    />
                    <FormHelperText error sx={{ px: 2, textAlign: 'center' }}>
                      {touched.images && errors.images && (
                        <FormHelperText error sx={{ px: 2 }}>
                          {touched.images && errors.images}
                        </FormHelperText>
                      )}
                    </FormHelperText>
                  </Box>
                </CardContent>
              </Card>
              <Card>
                <CardHeader title="Thumbnail" />
                <CardContent>
                  <Box sx={{ mb: 5 }}>
                    <UploadAvatar
                      accept="image/*"
                      file={values.thumbnail}
                      maxSize={3145728}
                      onDrop={handleThumbnailDrop}
                      error={Boolean(touched.thumbnail && errors.thumbnail)}
                      caption={
                        <Typography
                          variant="caption"
                          sx={{
                            mt: 2,
                            mx: 'auto',
                            display: 'block',
                            textAlign: 'center',
                            color: 'text.secondary',
                          }}
                        >
                          Allowed *.jpeg, *.jpg, *.png, *.gif
                          <br /> max size of {fData(3145728)}
                        </Typography>
                      }
                    />
                    <FormHelperText error sx={{ px: 2, textAlign: 'center' }}>
                      {touched.thumbnail && errors.thumbnail}
                    </FormHelperText>
                  </Box>
                </CardContent>
              </Card>
            </Stack>
          </Grid>

          <Grid item xs={12} md={6}>
            <Card>
              <CardContent>
                <Stack spacing={3}>
                  <Stack direction={{ xs: 'column', sm: 'row' }} spacing={{ xs: 3, sm: 2 }}>
                    <TextField
                      fullWidth
                      size="small"
                      label="Name"
                      {...getFieldProps('name')}
                      error={Boolean(touched.name && errors.name)}
                      helperText={touched.name && errors.name}
                    />
                    <TextField
                      select
                      fullWidth
                      size="small"
                      label="Giveaway Type"
                      {...getFieldProps(`giveawayType`)}
                      SelectProps={{ native: true }}
                      error={Boolean(touched.giveawayType && errors.giveawayType)}
                      helperText={touched.giveawayType && errors.giveawayType}
                    >
                      {['Level2', 'Level3', 'Brand Prize Set', 'Task Specific'].map((option) => (
                        <option key={option} value={option}>
                          {option}
                        </option>
                      ))}
                    </TextField>
                  </Stack>
                  <Stack>
                    <TextField
                      select
                      fullWidth
                      size="small"
                      label="Select Brand"
                      placeholder="Brand"
                      {...getFieldProps('brand')}
                      SelectProps={{ native: true }}
                      error={Boolean(touched.brand && errors.brand)}
                      helperText={touched.brand && errors.brand}
                    >
                      {['Adidas', 'Nike'].map((option, index) => (
                        <option key={option + index} value={option}>
                          {option}
                        </option>
                      ))}
                    </TextField>
                  </Stack>
                  <Stack direction={{ xs: 'column', sm: 'row' }} spacing={{ xs: 3, sm: 2 }}>
                    <Stack sx={{ width: '100%' }}>
                      <TextField
                        fullWidth
                        multiline
                        rows={3}
                        size="small"
                        label="Description"
                        {...getFieldProps('description')}
                        error={Boolean(touched.description && errors.description)}
                        helperText={touched.description && errors.description}
                      />
                    </Stack>
                    <Stack sx={{ width: '100%' }}>
                      <DateTimePicker
                        label="Start date"
                        value={values.startDate}
                        inputFormat="dd/MM/yyyy hh:mm a"
                        onChange={(date) => (isValidDate(date) ? setFieldValue('startDate', date) : null)}
                        renderInput={(params) => (
                          <TextField
                            {...params}
                            size="small"
                            fullWidth
                            error={Boolean(touched.startDate && errors.startDate)}
                            helperText={touched.startDate && errors.startDate}
                            sx={{ mb: 3 }}
                          />
                        )}
                      />
                      <FormControlLabel
                        labelPlacement="start"
                        control={<Switch {...getFieldProps('status')} checked={values.status} />}
                        label={<Typography variant="subtitle2">Active</Typography>}
                        sx={{ mx: 0, width: 1 }}
                      />
                    </Stack>
                  </Stack>
                  <Stack direction={{ xs: 'column', sm: 'row' }} spacing={{ xs: 3, sm: 2 }}>
                    <TextField
                      fullWidth
                      type="number"
                      size="small"
                      label="Value of bonus"
                      InputProps={{
                        endAdornment: <InputAdornment position="end">$</InputAdornment>,
                      }}
                      {...getFieldProps('value')}
                      error={Boolean(touched.value && errors.value)}
                      helperText={touched.value && errors.value}
                    />
                    <TextField
                      fullWidth
                      type="number"
                      size="small"
                      label="Available Quantity"
                      {...getFieldProps('availableQuantity')}
                      error={Boolean(touched.availableQuantity && errors.availableQuantity)}
                      helperText={touched.availableQuantity && errors.availableQuantity}
                    />
                  </Stack>

                  {isEdit && (
                    <Stack direction={{ xs: 'column', sm: 'row' }} spacing={{ xs: 3, sm: 2 }} alignItems="center">
                      <Typography>Associated Task: </Typography>
                      <Link href={PATH_DASHBOARD.campaign.detail} variant="h6">
                        Task 1
                      </Link>
                      <FormControlLabel
                        labelPlacement="start"
                        control={<Switch />}
                        label={<Typography variant="subtitle2">Task On/Off</Typography>}
                      />
                    </Stack>
                  )}

                  <Box sx={{ mt: 3, display: 'flex', justifyContent: 'flex-end' }}>
                    <LoadingButton type="submit" variant="contained" loading={isSubmitting} endIcon={<SaveIcon />}>
                      Save
                    </LoadingButton>
                  </Box>
                </Stack>
              </CardContent>
            </Card>
          </Grid>
        </Grid>
      </Form>
    </FormikProvider>
  );
}
